<template>
  <div>
    <b-button variant="outline-info" @click="nonStacked"
      >Tipologia tabella</b-button
    >

    <div v-if="files.length == 0"><p>Nessun documento</p></div>

    <!-- SE CELLULARE stacked hover-->
    <b-container fluid v-else-if="ruolo == 'uploader'">
      <b-table
        id="tableT"
        striped
        responsive
        sticky-header="600px"
        :items="files"
        :fields="fieldsU"
        :tbody-tr-class="highlightNew"
      >
        <template #cell(actions)="row">
          <b-button @click="$emit('del-file', row.item.id)" class="del">
            Elimina
          </b-button>
        </template>
      </b-table>
    </b-container>

    <b-container fluid v-else>
      <!--fluid="sm" style="max-width:100%;"-->
      <b-table
        id="tableT"
        striped
        responsive
        sticky-header="600px"
        :items="files"
        :fields="fieldsC"
        :tbody-tr-class="highlightNew"
      >
        <template #cell(actions)="row">
          <b-button @click="$emit('download-file', row.item.id)">
            Download
          </b-button>
        </template>
      </b-table>
    </b-container>
  </div>
</template>

<script>
// import FilesUploaderItem from "./FilesUploaderItem";
// import FilesConsumerItem from "./FilesConsumerItem";

export default {
  name: "Files",
  components: {
    // FilesConsumerItem,
    // FilesUploaderItem,
  },
  props: ["files", "ruolo"],
  data() {
    return {
      fieldsU: [
        "id",
        "name",
        {
          key: "dataCaricamento",
          sortable: true,
        },
        {
          key: "dataVisualizzazione",
          sortable: true,
        },
        "indirizzoIP",
        "hashtag",
        "actions",
      ],
      fieldsC: [
        "id",
        "name",
        {
          key: "dataCaricamento",
          sortable: true,
        },
        {
          key: "dataVisualizzazione",
          sortable: true,
        },
        "hashtag",
        "actions",
      ],
    };
  },
  methods: {
    highlightNew(item, type) {
      if (!item || type !== "row") return;
      if (item.dataVisualizzazione === "") return "table-warning";
    },
    nonStacked() {
      //todo: lasciare?
      let x = document.getElementById("tableT");
      let classeVoluta = " b-table-stacked";
      if (x.className.includes(classeVoluta))
        x.className = x.className.replace(classeVoluta,"");
      else x.className = x.className.concat(classeVoluta);
    },
  },
};
</script>

<style>
/* td {
  padding: 10px;
  border: 1px solid #333;
} */
.del {
  background: brown;
  color: #fff;
}
</style>
